import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HomeComponent } from './home/home.component';
import { UserComponent } from './user/user.component';
import { SignUpComponent } from './user/sign-up/sign-up.component';
import { SignInComponent } from './user/sign-in/sign-in.component';
import { AboutusComponent } from './aboutus/aboutus.component';
import { ContactusComponent } from './contactus/contactus.component';
import { ZebulunServicesComponent } from './zebulun-services/zebulun-services.component';
import { ManagementComponent } from './management/management.component';
import { GalleryComponent } from './zebu-images/gallery/gallery.component';

const routes: Routes = [

  { path: '', component: HomeComponent, pathMatch: 'full' },

  { path: 'home', component: HomeComponent},
  { path: 'aboutus', component: AboutusComponent },
  { path: 'services', component: ZebulunServicesComponent },
  { path: 'contactus', component: ContactusComponent },
  { path: 'management', component: ManagementComponent },
  { path: 'images', component: GalleryComponent },
  { path: 'images/:category', component: GalleryComponent },  
  { path: 'zebulun-users', redirectTo:'/login', pathMatch : 'full'},
  {
    path: 'signup', component: UserComponent,
    children: [{ path: '', component: SignUpComponent }]
  },
  {
      path: 'login', component: UserComponent,
      children: [{ path: '', component: SignInComponent }]
  },

];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
