import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
import { ToastrModule } from 'ngx-toastr';

import { AppComponent } from './app.component';
import { HomeComponent } from './home/home.component';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { LayoutComponent } from './layout/layout.component';
import { FooterComponent } from './footer/footer.component';
import { ContactusComponent } from './contactus/contactus.component';
import { HeaderComponent } from './navigation/header/header.component';
import { SidenavListComponent } from './navigation/sidenav-list/sidenav-list.component';
import { AppRoutingModule } from './app-routing.module';
import { FlexLayoutModule } from '@angular/flex-layout';
import { MaterialModule } from './material/material.module';
import { SignInComponent } from './user/sign-in/sign-in.component';
import { UserComponent } from './user/user.component';
import { SignUpComponent } from './user/sign-up/sign-up.component';
import { AboutusComponent } from './aboutus/aboutus.component';
import { ZebulunServicesComponent } from './zebulun-services/zebulun-services.component';
import { ManagementComponent } from './management/management.component';
import { ImageListComponent } from './zebu-images/image-list/image-list.component';
import { ImageNavigationComponent } from './zebu-images/image-navigation/image-navigation.component';
import { GalleryComponent } from './zebu-images/gallery/gallery.component';
import { EmailService } from './shared/services/email-service/email.service';
import { ForbidenComponent } from './forbiden/forbiden.component';
import { BannerComponent } from './banner/banner.component';

@NgModule({
  declarations: [
    AppComponent,
    HomeComponent,
    LayoutComponent,
    FooterComponent,
    ContactusComponent,
    HeaderComponent,
    SidenavListComponent,
    SignInComponent,
    UserComponent,
    SignUpComponent,
    AboutusComponent,
    ZebulunServicesComponent,
    ManagementComponent,
    GalleryComponent,
    ImageListComponent,
    ImageNavigationComponent,
    ForbidenComponent,
    BannerComponent
  ],
  imports: [
    BrowserModule.withServerTransition({ appId: 'ng-cli-universal' }),
    HttpClientModule, MaterialModule, 
    FormsModule, ReactiveFormsModule, AppRoutingModule, FlexLayoutModule,

    BrowserAnimationsModule, ToastrModule.forRoot({
      progressBar:true
    })
  ],
  providers: [EmailService],
  bootstrap: [AppComponent]
})
export class AppModule { }
